@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&display=swap);


body {
  background-color: #f8f8f8;
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  color: #333;
  font-size: 16px;
  line-height: 1em;
  margin: 0;
}

.App {
  display: inline;
}

p {
  margin: 0.3em;
}

a {
  color: #0084ff;
}

#sidebar {
  background-color: #1f1f1f;
  width: 25%;
  float: left;
  height: 100vh;
  position: fixed;
  padding-top: 20px;
  text-align: center;
  color: #f4f4f4;
}

#sidebar > img {
  border-radius: 100px;
}

#sidebar h2 {
  color: #c4c4c4;
  font-weight: 400;
}

#main-section {
  width: 70%;
  float: right;
  padding-left: 200px;
}

a {
  text-decoration: none;
}

.card {
  background: white;
  border-radius: 18px;
  box-shadow: 1px 1px 6px #aaa;
  padding: 20px;
  width: 85%;
  margin: auto;
  margin-top: 2em;
  margin-bottom: 2em;
}

.card h4 {
  font-weight: 600;
}

.header {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#summary {
  width: 70%;
  margin: auto;
  background-color: #b1b1b1;
  border-radius: 18px;
  font-size: 18px;
  color: #444;
  padding: 10px;
  line-height: 1.3em;
}

